import React from 'react';
import { Link } from 'react-router-dom';
import { Wrapper } from './styles';

function Profile() {
	return (
		<Wrapper>
			<div className="profiles">
				<h1 className="profiles-header">
					   .
				</h1>
				<ul className="profiles-list">
					<li className="profiles-item">
						<Link to="/">
							<div className="profile">
								<div className="profile-img red" />
								<h3 className="profile-name">1 </h3>
							</div>
						</Link>
					</li>
					<li className="profiles-item">
						<Link to="/">
							<div className="profile">
								<div className="profile-img green" />
								<h3 className="profile-name">2</h3>
							</div>
						</Link>
					</li>
					<li className="profiles-item">
						<Link to="/">
							<div className="profile">
								<div className="profile-img blue" />
								<h3 className="profile-name">3</h3>
							</div>
						</Link>
					</li>
				</ul>
				<div className="profiles-manager">
					<Link to="/">
						<span> </span>
					</Link>
				</div>
			</div>
		</Wrapper>
	);
}

export default Profile;
